<template>
  <div id="micYangImpotDIV">
      <my-header :log="true"></my-header>
      <div class="ho-content">
          <div class="ho-nav" v-if="switchNV">
              <my-location title="MIC信息-阳性"></my-location>
              <my-navigat activ="1-4"></my-navigat>
          </div>
          <div class="ho-cotet" :style="cotDivWidth">
              <div class="inputGp" v-show="switchSH">
                  <el-upload
                    :action="uploadURL"
                    :on-preview="handlePreview"
                    :on-remove="handleRemove"
                    :on-success="succeedUpload"
                    :on-error="failUpload"
                    :default-file-list="fileList">
                    <el-button size="small" type="primary">点击上传</el-button>
                    <div class="el-upload__tip" slot="tip">只能上传excel文件，excel的格式模板可点击下方按钮下载</div>
                  </el-upload>
              </div>
              <div class="naigatDIV">
                  <el-switch v-model="switchNV" on-text="导航" off-text="导航" :width="60">
                  </el-switch>
              </div>
              <div class="switchDIV">
                  <el-switch v-model="switchSH" on-text="查询" off-text="查询" :width="60">
                  </el-switch>
              </div>
              <div class="operatBtnG">
                  <el-button-group>
                    <el-button icon="d-arrow-left" @click="goBack">返回</el-button>
                    <el-button icon="caret-bottom" type="primary" @click="downLoadFile">下载导入模板</el-button>
                  </el-button-group>
                  <el-button-group>
                    <el-button type="primary" icon="edit" @click="onEdit" style="border-radius:4px;">修改</el-button>
                  </el-button-group>
              </div>
              <div class="tableDIV">
                  <el-table
                    :data="tableData"
                    border
                    style="width: 100%;"
                    max-height="600"
                    @select="deleteRow"
                    @selection-change="handleSelectionChange">
                    <el-table-column
                      type="selection"
                      fixed
                      width="35">
                    </el-table-column>
                    <el-table-column
                      prop="TB_id"
                      label="菌株编号"
                      width="120">
                    </el-table-column>
                    <el-table-column
                      prop="TB_inno"
                      label="内部编号"
                      width="100">
                    </el-table-column>
                    <el-table-column
                      prop="AMP"
                      label="AMP"
                      width="52">
                    </el-table-column>
                    <!-- <el-table-column
                      :prop="item"
                      :label="item"
                      width="52"
                      v-for="item in YangSeq">
                    </el-table-column> -->
                    <el-table-column
                      prop="CIP"
                      label="CIP"
                      width="52">
                    </el-table-column>
                    <el-table-column
                      prop="CLI"
                      label="CLI"
                      width="52">
                    </el-table-column>
                    <el-table-column
                      prop="CQM"
                      label="CQM"
                      width="52">
                    </el-table-column>
                    <el-table-column
                      prop="ERY"
                      label="ERY"
                      width="52">
                    </el-table-column>
                    <el-table-column
                      prop="FFC"
                      label="FFC"
                      width="52">
                    </el-table-column>
                    <el-table-column
                      prop="FOX"
                      label="FOX"
                      width="52">
                    </el-table-column>
                    <el-table-column
                      prop="GEN"
                      label="GEN"
                      width="52">
                    </el-table-column>
                    <el-table-column
                      prop="LZD"
                      label="LZD"
                      width="52">
                    </el-table-column>
                    <el-table-column
                      prop="OXA"
                      label="OXA"
                      width="52">
                    </el-table-column>
                    <el-table-column
                      prop="PEN"
                      label="PEN"
                      width="52">
                    </el-table-column>
                    <el-table-column
                      prop="RIF"
                      label="RIF"
                      width="52">
                    </el-table-column>
                    <el-table-column
                      prop="TET"
                      label="TET"
                      width="52">
                    </el-table-column>
                    <el-table-column
                      prop="TIA"
                      label="TIA"
                      width="52">
                    </el-table-column>
                    <el-table-column
                      prop="VAL"
                      label="VAL"
                      width="52">
                    </el-table-column>
                    <el-table-column
                      prop="VAN"
                      label="VAN"
                      width="52">
                    </el-table-column>
                    <el-table-column
                      prop="TB_man"
                      label="实验人员"
                      width="100">
                    </el-table-column>
                    <el-table-column
                      prop="TB_style"
                      label="实验方式"
                      width="100">
                    </el-table-column>
                    <el-table-column
                      prop="TB_remark"
                      label="备注">
                    </el-table-column>
                    <el-table-column
                      prop="TB_failReason"
                      fixed="right"
                      label="失败原因"
                      width="300">
                    </el-table-column>
                  </el-table>
              </div>
              <el-dialog title="MIC耐药性信息-阳性" v-model="dialogFormVisible" size="large">
                  <el-form :model="formAdd" :rules="formAddRules" ref="formAdd" :inline="true">
                    <el-form-item label="菌株编号" :label-width="formLabelWidth" prop="id">
                      <el-input v-model="formAdd.id" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="实验人员" :label-width="formLabelWidth" prop="man">
                      <el-input v-model="formAdd.man" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="AMP" :label-width="formLabelWidth" prop="AMP">
                        <el-input v-model="formAdd.AMP" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="CIP" :label-width="formLabelWidth" prop="CIP">
                        <el-input v-model="formAdd.CIP" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="CLI" :label-width="formLabelWidth" prop="CLI">
                        <el-input v-model="formAdd.CLI" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="CQM" :label-width="formLabelWidth" prop="CQM">
                        <el-input v-model="formAdd.CQM" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="ERY" :label-width="formLabelWidth" prop="ERY">
                        <el-input v-model="formAdd.ERY" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="FFC" :label-width="formLabelWidth" prop="FFC">
                        <el-input v-model="formAdd.FFC" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="FOX" :label-width="formLabelWidth" prop="FOX">
                        <el-input v-model="formAdd.FOX" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="GEN" :label-width="formLabelWidth" prop="GEN">
                        <el-input v-model="formAdd.GEN" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="LZD" :label-width="formLabelWidth" prop="LZD">
                        <el-input v-model="formAdd.LZD" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="OXA" :label-width="formLabelWidth" prop="OXA">
                        <el-input v-model="formAdd.OXA" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="PEN" :label-width="formLabelWidth" prop="PEN">
                        <el-input v-model="formAdd.PEN" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="RIF" :label-width="formLabelWidth" prop="RIF">
                        <el-input v-model="formAdd.RIF" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="TET" :label-width="formLabelWidth" prop="TET">
                        <el-input v-model="formAdd.TET" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="TIA" :label-width="formLabelWidth" prop="TIA">
                        <el-input v-model="formAdd.TIA" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="VAL" :label-width="formLabelWidth" prop="VAL">
                        <el-input v-model="formAdd.VAL" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="VAN" :label-width="formLabelWidth" prop="VAN">
                        <el-input v-model="formAdd.VAN" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="实验方式" :label-width="formLabelWidth" prop="style">
                      <el-input v-model="formAdd.style" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="备注" :label-width="formLabelWidth" prop="remark">
                      <el-input v-model="formAdd.remark" auto-complete="off"></el-input>
                    </el-form-item>
                  </el-form>
                  <div slot="footer" class="dialog-footer">
                    <el-button @click="resetForm('formAdd')">取 消</el-button>
                    <el-button type="primary" @click="onConfirm">确 定</el-button>
                  </div>
              </el-dialog>
              <div class="clearfix"></div>
          </div>
      </div>
  </div>
</template>

<script>
import myHeader from './../../components/header'
import myLocation from './../../components/location'
import myNavigat from './../../components/navigat'
import axios from 'axios'
import dataSet from './../../publicAPI/dataSet'
import dataRules from './../../publicAPI/dataRules'
import config from './../../publicAPI/config'

export default {
  components: {
      myHeader,
      myLocation,
      myNavigat,
  },
  data() {
      return {
          URL: config.rootURL,
          uploadURL: config.rootURL + '/mic/positive/upload',
          tableData: [],
          multipleSelection: [],
          dialogFormVisible: false,
          formAdd: {
              id: '',
              inno: '',
              AMP: '',
              CIP: '',
              CLI: '',
              CQM: '',
              ERY: '',
              FFC: '',
              FOX: '',
              GEN: '',
              LZD: '',
              OXA: '',
              PEN: '',
              RIF: '',
              TET: '',
              TIA: '',
              VAL: '',
              VAN: '',
              man: '',
              style: '',
              remark: '',
          },
          formAddRules: {
              id: [
                  { required: true, message: '请输入菌株编号', trigger: 'blur' },
                  { min: 1, max: 15, message: '长度在 1 到 15 个字符', trigger: 'blur' }
              ]
          },
          switchSH: true,   //查询
          switchNV: true,   //导航
          loading2: false,
          formLabelWidth: '84px',
          cotDivWidth:{
              width: '1150px',
          },
          YangDrugSet: [],
          YangSeq: [],
          fileList: [
          ],
          selectRow: '',
      };
  },
  methods:{
        handleRemove(file, fileList) {
            console.log(file, fileList);
        },
        handlePreview(file) {
            console.log(file);
        },
        deleteRow(selection, row){
            this.selectRow = row.TB_id
        },
        onEdit(){
            if(this.multipleSelection.length==1){
                this.dialogFormVisible = true
                this.formAdd = {
                    id: this.multipleSelection[0].TB_id,
                    AMP: this.multipleSelection[0].AMP,
                    CIP: this.multipleSelection[0].CIP,
                    CLI: this.multipleSelection[0].CLI,
                    CQM: this.multipleSelection[0].CQM,
                    ERY: this.multipleSelection[0].ERY,
                    FFC: this.multipleSelection[0].FFC,
                    FOX: this.multipleSelection[0].FOX,
                    GEN: this.multipleSelection[0].GEN,
                    LZD: this.multipleSelection[0].LZD,
                    OXA: this.multipleSelection[0].OXA,
                    PEN: this.multipleSelection[0].PEN,
                    RIF: this.multipleSelection[0].RIF,
                    TET: this.multipleSelection[0].TET,
                    TIA: this.multipleSelection[0].TIA,
                    VAL: this.multipleSelection[0].VAL,
                    VAN: this.multipleSelection[0].VAN,
                    man: this.multipleSelection[0].TB_man,
                    style: this.multipleSelection[0].TB_style,
                    remark: this.multipleSelection[0].TB_remark,
                }
            }
        },
        onConfirm(){
            let that = this
            this.$refs['formAdd'].validate((valid) => {
                  if (valid) {
                      var param = {
                          strainno: that.formAdd.id,
                          amp: parseFloat(that.formAdd.AMP),
                          cip: parseFloat(that.formAdd.CIP),
                          cli: parseFloat(that.formAdd.CLI),
                          cqm: parseFloat(that.formAdd.CQM),
                          ery: parseFloat(that.formAdd.ERY),
                          ffc: parseFloat(that.formAdd.FFC),
                          fox: parseFloat(that.formAdd.FOX),
                          gen: parseFloat(that.formAdd.GEN),
                          lzd: parseFloat(that.formAdd.LZD),
                          oxa: parseFloat(that.formAdd.OXA),
                          pen: parseFloat(that.formAdd.PEN),
                          rif: parseFloat(that.formAdd.RIF),
                          tet: parseFloat(that.formAdd.TET),
                          tia: parseFloat(that.formAdd.TIA),
                          val: parseFloat(that.formAdd.VAL),
                          van: parseFloat(that.formAdd.VAN),
                          operator: that.formAdd.man,
                          micdetectiontype: that.formAdd.style,
                          remark: that.formAdd.remark
                      }
                    //   var str = JSON.stringify(param)
                      axios.post(that.URL+'/mic/positive/addition', param)
                      .then(function (res) {
                          if(res.data.state){
                              that.$message({
                                type: 'success',
                                message: res.data.value
                              });
                              that.tableData.forEach((item, index) => {
                                //   console.log(item.TB_id+' --- '+that.selectRow)
                                  if(item.TB_id == that.selectRow){
                                      that.tableData.splice(index, 1);
                                  }
                              });
                          }else{
                              that.$message({
                                type: 'error',
                                message: res.data.value
                              });
                          }
                      })
                      .catch(function (error) {
                          console.error(error);
                      });
                      this.dialogFormVisible = false
                  } else {
                    console.log('error submit!!');
                    return false;
                  }
            });
        },
        resetForm(formName){
            this.$refs[formName].resetFields();
            this.formAdd = {
                id: '',
                inno: '',
                AMP: '',
                CIP: '',
                CLI: '',
                CQM: '',
                ERY: '',
                FFC: '',
                FOX: '',
                GEN: '',
                LZD: '',
                OXA: '',
                PEN: '',
                RIF: '',
                TET: '',
                TIA: '',
                VAL: '',
                VAN: '',
                man: '',
                style: '',
                remark: '',
            }
            this.dialogFormVisible = false
        },
        succeedUpload(response, file, fileList){
            let that = this;
            if(response.state){
                this.$message({
                  message: response.value,
                  duration: 5000,
                  type: 'success'
                });

            }else{
                if(typeof(response.value) != 'string'){
                    this.$message({
                      message: '成功导入【' + response.value.success + '】条，有【' + response.value.error + '】条导入失败！',
                      duration: 5000,
                      type: 'warning'
                    });
                    /*插入数据*/
                    that.tableData = [];
                    (response.value.data).forEach( ( item, index) => {
                        that.tableData.push({
                            TB_id: item.strainno,
                            TB_inno: item.micalias,
                            AMP: item.amp,
                            CIP: item.cip,
                            CLI: item.cli,
                            CQM: item.cqm,
                            ERY: item.ery,
                            FFC: item.ffc,
                            FOX: item.fox,
                            GEN: item.gen,
                            LZD: item.lzd,
                            OXA: item.oxa,
                            PEN: item.pen,
                            RIF: item.rif,
                            TET: item.tet,
                            TIA: item.tia,
                            VAL: item.val,
                            VAN: item.van,
                            TB_man: item.operator,
                            TB_style: item.micdetectiontype,
                            TB_remark: item.remark,
                            TB_failReason: item.otherMsg,
                        });
                        // console.log(JSON.stringify(that.tableData))
                    });
                }else{
                    this.fileList = [];
                    this.$message({
                      message: response.value,
                      type: 'error'
                    });
                }
            }
        },
        failUpload(err, response, file){
            this.fileList = [];
            this.$message({
              message: err,
              type: 'error'
            });
        },
        goBack(){
            this.$router.push({ path: '/micInfoYang' })
        },
        downLoadFile(){
            window.open('/drug/static/uploads/Positive_mic.xls')
        },
        handleSelectionChange(val) {
            this.multipleSelection = val;
        },
   },
   watch:{
       switchNV(val){
           if(val){
              this.cotDivWidth = {
                   width: '1150px',
              }
          }else{
              this.cotDivWidth = {
                   width: '100%',
              }
          }
      },
   }
}
</script>

<style scoped>
.ho-cotet{
    /*width: ;*/
    float: right;
    position: relative;
    padding: 30px 30px 50px;
}
.el-button{
    padding: 9px 14px;
}
/*操作按钮组*/
.operatBtnG{
    margin-top: 20px;
    margin-bottom: 30px;
}
.tableDIV{
    /*width: 1030px;*/
}
/*全局盒子控制*/
.sep1150{
    width: 1150px !important;
}
.sep100per{
    width: 100% !important;
}
/*分页*/
.block{
    margin-top: 40px;
}
/*对话框*/
.el-dialog .el-form-item .el-input, .el-dialog .el-form-item .el-select{
    width: 180px ;
}
</style>



