<!-- 首页 | created by JanvierCheng -->
<template>
  <div id="home">
      <my-header :log="true"></my-header>
      <div class="ho-content">
          <div class="ho-nav">
              <my-location title="首页"></my-location>
              <my-navigat></my-navigat>
          </div>
          <div class="ho-cotet">
              <div class="ho-DL">
                  <h2>资源下载</h2>
                  <div>
                      <label>用户手册</label>
                      <el-button type="primary">下载</el-button>
                  </div>
                  <div>
                      <label>打印机本地驱动</label>
                      <el-button type="primary">下载</el-button>
                  </div>
                  <div>
                      <label>网页打印驱动</label>
                      <el-button type="primary">下载</el-button>
                  </div>
                  <div>
                      <label>打印机设置手册</label>
                      <el-button type="primary">下载</el-button>
                  </div>
              </div>
              <div class="ho-LINK">
                  <h2>热门链接</h2>
                  <ul>
                      <li v-for="item in link"><a :href="item.url" target="_blank"> {{ item.name }} </a></li>
                  </ul>
              </div>
              <div class="clearfix"></div>
          </div>
      </div>

  </div>
</template>

<script>
import myHeader from './components/header'
import myLocation from './components/location'
import myNavigat from './components/navigat'
import axios from 'axios';
import config from './publicAPI/config'

export default {
  // name: 'login',
  components: {
      myHeader,
      myLocation,
      myNavigat
  },
  data(){
      return{
          URL: config.rootURL,
          link: [],
      }
  },
  methods:{
      getLink(){
          let that = this
          axios.post(that.URL + '/linkurl/list')
            .then((res) => {
                if(res.data.state){
                    that.link = res.data.value.data
                }else{
                    that.$message.error(res.data.value);
                }

                // console.log(JSON.stringify(that.link))
            })
            .catch((error) => {
                console.error(error);
            })
      }
  },
  created(){

      this.getLink()
  }
}
</script>

<style scoped>
#home {
}

.el-button{
    padding: 10px 30px;
    font-family: 'Microsoft Yahei' !important;
}
.ho-LINK{
    width: 450px;
    float: left;
}
.ho-LINK h2{
    font-weight: normal;
    margin-bottom: 24px;
}
.ho-LINK ul{

}
.ho-LINK ul li{
    margin: 10px 0;
}
.ho-LINK ul li a{
    display: inline-block;
    font-size: 15px;
    cursor: pointer;
}
.ho-LINK ul li a:hover{
    color: #1D8CE0;
}
</style>



